@media (max-width: 767px) {
  .tpl-pages-business {
   
      .page-banner-content {
       height: 0px;

        h1 {   
       
          font-size: 28px;
        }
        h2 {
          font-size: 16px;
        }
      
    }
  }  
} 
  
 @media (min-width: 768px) {

   .tpl-pages-business {
      .page-banner-content {
        top: 350px;

        h1 {
          
          font-size: 48px;
          line-height: 1.2em;
    
        }
        
        h2 {
          font-size: 22px;
          line-height: 1.5em;    
          width: 860px;
        }
