.section-storage {
  position: relative;
}
.section-storage::before {
  background-color: #fff;
  content: "";
  display: block;
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  -webkit-transition: visiblity 0s linear 0.5s, opacity 0.3s ease-in-out;
  -o-transition: visiblity 0s linear 0.5s, opacity 0.3s ease-in-out;
  transition: visiblity 0s linear 0.5s, opacity 0.3s ease-in-out;
  visibility: hidden;
  width: 100%;
  z-index: 100;
}
.section-storage .postal_code-form,
.section-storage .storing-duration {
  -webkit-transition: visiblity 0s linear 0.5s, opacity 0.3s ease-in-out;
  -o-transition: visiblity 0s linear 0.5s, opacity 0.3s ease-in-out;
  transition: visiblity 0s linear 0.5s, opacity 0.3s ease-in-out;
}
.section-storage .postal_code-form {
  opacity: 0;
  position: absolute;
  visibility: hidden;
}
.section-storage .storing-duration {
  border: 2px solid #f2f2f2;
  opacity: 1;
  visibility: visible;
}
.section-storage.blocked {
  overflow: hidden;
}
.section-storage.blocked::before {
  opacity: 0.6;
  visibility: visible;
}
.section-storage.blocked .postal_code-form {
